import React , {useState} from 'react'
import styled from 'styled-components';
import {TriangleDown} from '@styled-icons/entypo/TriangleDown'
import {TriangleUp} from '@styled-icons/entypo/TriangleUp'
import DropDownMenu from './DropDownMenu';
import { AiOutlineSearch } from 'react-icons/ai';



const Navbar = () => {

    const [isOpen, setIsOpen] = useState(false)

    const [isVisible, setIsVisible] = useState(false);

    const toggleVisibility = () => {
    setIsVisible(!isVisible);
    };

    


    return (
    <NavBarContainer>
        <Nav>
            <Logo href='/#'>venüs eğitim </Logo>
            <Hamburger onClick={()=> setIsOpen(!isOpen)}>
                <span/>
                <span/>
                <span/>

            </Hamburger>
            <Menu isOpen={isOpen}>
                <SearchBar>
                    <Courses onClick={toggleVisibility}>
                        <a href='/#'>Kurslar</a>
                        {isVisible ? <TersUcgen /> : <DuzUcgen />}
                        
                    </Courses>
                    
                    <input placeholder='Kursun ismini giriniz...' ></input>
                    
                    <a href='/#'><SearchIcon/></a>
                </SearchBar>
                <MenuLink href=''>Girişhfchch Yap</MenuLink>
                <MenuLink href=''>Kayıt Ol</MenuLink>
                <MenuLink href=''>Bağış Yap</MenuLink>
            </Menu>
            
            {isVisible && <DropDownMenu/>}
            
            

        </Nav>
    </NavBarContainer>
    )
};

const NavBarContainer = styled.div`
    background-color: #efecf3;
    width: 100vw;
    display: flex;
    justify-content: center;
    @import url('https://fonts.cdnfonts.com/css/poetsen-one');
    font-family: 'Poetsen One', sans-serif;
    font-style: normal;

`

const Nav = styled.div`
    max-width: 1200px;
    width: 1200px;
    background-color: #efecf3;    
    margin: 0 3rem;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;

`
const Hamburger = styled.div`
    display: none;
    flex-direction: column;
    cursor: pointer;

    span{
        height: 2px;
        width: 25px;
        background-color:#674188;
        margin-bottom: 4px;
        border-radius: 5px;
        }
    
    @media (max-width: 768px) {
    display: flex;
    }
`

const SearchBar = styled.div`
    border: 2px solid #674188;
    padding: .3rem 1rem;
    display: flex;
    justify-content: center;
    border-radius: 15px;
    margin-right: 4rem;
    max-width: 100%;

    @media (max-width: 768px) {
        margin-right: 0;
    }
    

    @media (min-width: 768px) and (max-width: 1100px){
        padding: 0.3rem .4rem;
        margin-right: 0;
    }
    

    input{
        width: 12rem; 
        border-radius: 5px;
        border: .1rem solid #674188;
        margin-right: 1rem;
        padding-left: 0.7rem;
        @media (min-width: 768px) and (max-width: 1100px){
        display: none;
        font-size: 1rem;
    }
    }

    input::placeholder{
        
        font-size: 1rem;
        opacity: .6;
        
    }

    
    a{
        text-decoration: none;
        color: #674188;
        @import url('https://fonts.cdnfonts.com/css/poetsen-one');
        font-family: 'Poetsen One', sans-serif;
        font-style: normal;
        
    }
`
const Courses = styled.button`
    display: flex;
    cursor: pointer;
    justify-content: center;
    align-items: center;
    border: none;
    background-color: transparent;
    

    &:hover{
            color: #000000;
    }

    
    
    @media (min-width: 768px) and (max-width: 1100px){
        display: none;
    }
    
    

        a{
            font-size: 1.1rem;
            width: 70px;
            
            
            @media (max-width: 768px) {
        
                    font-size: .9rem;
    
                        }

            
        }
    `


const TersUcgen = styled(TriangleDown)`
    color: #674188;
    height: 1.6rem;
    margin-right: 1rem;    
    display: flex;

    @media (max-width: 768px) {
        
        margin-right: 0rem;

            }
    
`
const DuzUcgen = styled(TriangleUp)`
    color: #674188;
    height: 1.6rem;
    margin-right: 1rem;  
    display: flex;
    margin-bottom: -0.1rem;

    @media (max-width: 768px) {
        
        margin-right: 0rem;

            }
    
`

const SearchIcon = styled(AiOutlineSearch)`
    width: 1.6rem;
    height: 1.6rem;
    


`

const Logo = styled.a`
    @import url('https://fonts.cdnfonts.com/css/poetsen-one');
    font-family: 'Poetsen One', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 2.5rem;
    padding: 1rem 0;
    color: #674188;
    text-decoration: none;
    margin-right: 5rem;

    @media (max-width: 768px) {
        font-size: 1.5rem;
    }

    
`
const Menu = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;

    @media (max-width: 768px) {
        overflow: hidden;  
        flex-direction: column;
        width: 100%;
        max-height: ${({isOpen}) => (isOpen ? '300px' : '0')};
        transition: max-height 0.5s ease-in;
    }
    
`
const MenuLink = styled.a`
    padding: 1rem .5rem;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    @import url('https://fonts.cdnfonts.com/css/poetsen-one');
    font-family: 'Poetsen One', sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 1rem;

    

    &:hover{
        background-color: #674188;
        color: #efecf3;
        border-radius: 5px;
        
        
    }
`

export default Navbar;

