import React from 'react';
import styled from 'styled-components';

const DropDownMenu = () => {
    return (
        <A>
            <B>
                <Column>
                    <Baslik>TYT Matematik</Baslik>
                    <ul>
                        <li>asd</li>
                        <li>asdasd</li>
                        <li>asdasd</li>
                        <li>asdasds</li>

                    </ul>
                </Column>

                <Column><Baslik>AYT Matematik</Baslik></Column>
                <Column>
                    <Row><Baslik>12.Sınıf</Baslik></Row>
                    <Row><Baslik>10.Sınıf</Baslik></Row>
                </Column>
                <Column>
                    <Row><Baslik>11.Sınıf</Baslik></Row>
                    <Row><Baslik>9.Sınıf</Baslik></Row>
                </Column>
            </B>

        
        </A>
    );
};

export default DropDownMenu;



const A = styled.div`
    background-color: #efecf3;
    width: 100vw;
    display: flex;
    justify-content: center;
    @import url('https://fonts.cdnfonts.com/css/poetsen-one');
    font-family: 'Poetsen One', sans-serif;
    font-style: normal;
    z-index: 1;

    `
    

const B = styled.div`
    max-width: 1200px;
    position: absolute;
    top: 100px;
    border: 2px solid #ccc;
    width: 100%;
    height: 1000px;
    margin: -2rem 2rem 0 2rem;
    background-color: #efecf3;
    display: flex;
    justify-content: space-between;
    font-size: 1rem;

    @media (max-width: 768px) {
        top: 300px;
    }
`

const Column = styled.div`
    flex: 1;
    padding: 10px;
    border: 2px solid #ccc;
    margin: 0 10px;
`

const Row = styled.div`
    border: 1px solid #ddd;
    padding: 5px;
    margin: 5px 0;
    height: 50%;

`
const Baslik = styled.a`
    text-decoration: none;
    font-size: 1.3rem;
    font-style: bold;
`
