import React from 'react'
import styled from 'styled-components';
import ChangingText from './ChangingText';
import mainPicture from "../images/anaresim11.svg"; // Assuming the file path is correct
import ButtonStudent from "./buttons/buttonStudent";




function MainMenu() {
    return (
    <Container>
        <Menu>
            <Text>
                <p style={{paddingBottom:"15px"}}>Türkiye'nin   </p>
                <ChangingText ></ChangingText>                
                <p style={{paddingBottom:"15px"}}>Ücretsiz Eğitim</p>
                <p >Platformu</p><br/>
                <Text2>Her öğrencinin eğitime ulaşma hakkı  olduğu fikri ile çıktığımız bu yolda bize katıl.</Text2>
                <div><ButtonStudent/></div>
            </Text>
            <Picture>
                <img src={mainPicture} alt='resim'></img>
            </Picture>
        </Menu>
        
    </Container>
    )
}



const Container = styled.div`
    display: flex;
    justify-content: center;
    background-color: #fff;
    width: 100vw;

    
`

const Menu = styled.div`
    display: flex;
    width: 1200px;
    height: auto;
    background-color: #fff;
    margin: 6rem 4rem;
    justify-content: space-between;
    





    @media (max-width: 768px){
        margin: .5rem 2rem;
        flex-direction: column-reverse;
        height: auto;
    }
`

const Text = styled.div`
    width: 600px;

    

    p{
        
        color: #292929;
        font-size: 58px;
        @import url('https://fonts.googleapis.com/css2?family=Agdasima&family=Narnoor&family=Noto+Serif+Makasar&family=Nunito:wght@200;300;400;500;600&family=Roboto&display=swap');
        font-family: 'Roboto', sans-serif;
        text-shadow: 0px 4px 2px rgba(0, 0, 0, 0.25);
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        background-color: white;
        line-height: 4rem;


    }

    @media (max-width: 768px){
        width: 100%;
        
        font-size: 45px;

        p{
            font-size: 35px;
            line-height:1.8rem;
        }
        
    }
`
const Text2 = styled.div`
    color: #000;
    text-align: left; 
    font-family: Roboto Condensed; 
    font-size: 32px; 
    font-style: normal; 
    font-weight: 300; 
    line-height: normal;
    margin-bottom: 1rem; 

    @media (max-width: 768px) {
        font-size: 25px;
        
        margin-top: -2rem;
        

        


    }

`

const Picture = styled.div`
    width: 400px;
    min-width: 370px;
    display:flex;
    justify-content: end;
    
    

    img{
        
        width: 100%;
        height: 100%;
        
    }

    @media (max-width: 768px){
        width: 100%;
        min-width: 100%;
        justify-content: center;
        

        img{
            width: 100%;
            
        }
    }
`






export default MainMenu;
